.home-container {
    position: relative;
    overflow: hidden;
}
.background {
    position: absolute; 
    top: 50%; 
    left: 50%; 
    transform: translate(-50%, -50%);
    text-align: center;
    padding: 20px; 
    color: white;
}
.main-title {
    font-size: 70px;
    color: #FFFFFF; 
}
.sub-title {
    font-size: 20px;
    color: #D9D9D9;
}
.catalog-button {
    font-family: 'Playfair Display', serif;
    padding: 16px 40px;
    background: #4d4d4d;
    color: white;
    font-size: 18px;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s;
}
.catalog-button:hover {
    background: #7A6A58;
}