.catalog {
  padding: 20px;
  text-align: center;
}

.filters {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.filter-button-group {
  display: flex;
  gap: 10px;
}

.filter-button-group button {
  font-family: 'Playfair Display', serif;
  background: linear-gradient(45deg, #da7d88, #f2b9b7);
  color: white;
  border: none;
  border-radius: 10px;
  padding: 12px 15px;
  font-size: 17px;
  cursor: pointer;
  transition: background 0.3s;
}

.filter-button-group button:hover {
  background: linear-gradient(45deg, #da7d88, #ebebeb);
}

.filter-button-group button.active {
  background: linear-gradient(45deg, #da7d88, #ffffff);
}

.sort-dropdown {
  display: flex;
}

.sort-dropdown select {
  font-family: 'Playfair Display', serif;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.catalog-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(360px, 1fr));
  gap: 50px;
  justify-items: center;
}

.product-card {
  border: 1px solid #e0e0e0;
  border-radius: 12px;
  padding: 15px;
  text-align: center;
  background: #f9f9f9;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s;
}

.product-card:hover {
  transform: scale(1.05);
}

.product-card img {
  max-width: 90%;
  height: auto;
  border-radius: 8px;
}

.product-card h3 {
  font-size: 1.6em;
  margin: 10px 0;
  color: #444;
}

.product-card p {
  font-size: 1em;
  color: #666;
}

.product-card button {
  font-family: 'Playfair Display', serif;
  background: linear-gradient(45deg, #da7d88, #f2b9b7);
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 25px;
  cursor: pointer;
  margin: 10px 0;
  transition: background 0.3s;
}

.product-card button:hover {
  background: linear-gradient(45deg, #ff6b6b, #f2b9b7);
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal {
  background: white;
  border-radius: 12px;
  padding: 30px;
  max-width: 400px;
  text-align: center;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
}

.modal img {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
}

.modal-close {
  background: transparent;
  border: none;
  font-size: 2em;
  cursor: pointer;
  position: absolute;
  top: 10px;
  right: 20px;
}

.modal-enter {
  opacity: 0;
  transform: scale(0.9);
}

.modal-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: all 0.3s ease;
}

.modal-exit {
  opacity: 1;
}

.modal-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: all 0.3s ease;
}