.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    background-color: #ffffff; 
    color: white; 
    box-shadow: 0 2px 5px rgba(0,0,0,0.2);
}
.header .logo {
    height: 45px;
    width: auto;
    margin-left: 10px;
    max-width: 100%;
}
.header nav {
    display: flex;
}
.header nav a {
    color: rgb(0, 0, 0);
    text-decoration: none;
    margin: 0 15px;
    transition: color 0.3s;
    font-size: 20px;
}
.header nav a:hover {
    color: #ffffff;
}