html, body {
  height: 100%; margin: 0;
}
.slider-container, .slick-slider, .slider-image {
  height: 100vh; width: 100%; object-fit: cover;
}
.slick-dots {
  bottom: 10px;
}
.slick-dots li button:before {
  font-size: 25px; color: #D9D9D9;
}
.slick-dots li.slick-active button:before {
  color: #4C3F30;
}
.slick-prev { left: 10px; }
.slick-next { right: 0; }
@keyframes fadeIn {
  to { opacity: 1; }
}
.title, .subtitle {
  margin: 0; padding: 20px 0; color: black;
  opacity: 0; animation: fadeIn 3s forwards;
}
.subtitle { font-size: 40px; padding: 15px 0; animation-delay: 0.5s; }
.title { font-family: 'Carl Brown', sans-serif; font-size: 128px; }